import React from 'react';
import { useNavigate } from 'react-router-dom';
import "./About.css";
import {NavbarSimple} from './Navbar.js'; 
import CATM from './CATM.png'

function About() {
    let navigate = useNavigate(); 
    const routeChange = () => { 
        let path = '/locations'; 
        navigate(path);
    }

    return(
        <div>
            <NavbarSimple />
            <div class="flex flex-col lg:flex-row h-screen">
                <div class="flex w-full lg:w-[60%] items-center justify-center p-2 leftPane flex-grow h-screen lg:h-auto">
                    <div class="p-6">
                        <div className='text-4xl font-bold'>Искаш да купуваш и продаваш криптовалута?</div>
                        <div className='text-2xl'>Нашите криптомати са идеалното решение за теб!</div>
                        <button type="button" onClick={routeChange} class="text-white bg-gradient-to-r from-purple-500 to-pink-500 hover:bg-gradient-to-l focus:ring-4 focus:outline-none focus:ring-purple-200 dark:focus:ring-purple-800 font-medium rounded-lg text-xl px-5 py-2.5 text-center me-2 mt-8">Започни сега!</button>
                    </div>
                </div>
                <div class="hidden lg:flex w-full lg:w-[40%] items-center justify-center p-2 rightPane flex-shrink-0 h-auto lg:h-full">
                    <div class="text-4xl p-6">
                        <img src={CATM}></img>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;