import React, {useState, useRef, useEffect, useCallback} from "react";
import './Locations.css';
import { Input, Button } from "@material-tailwind/react";
import {NavbarSimple} from './Navbar.js';
import {APIProvider, Map, MapCameraChangedEvent, useMap, AdvancedMarker, Pin, InfoWindow} from '@vis.gl/react-google-maps';
import {MarkerClusterer, Marker} from '@googlemaps/markerclusterer';
import sample1 from "./sample1.jpg";
import sample2 from "./sample2.jpg";
import sample3 from "./sample3.jpg";
import sample4 from "./sample4.jpg";

function Locations() {
  const API_KEY = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;

  const locations = [
    { key: 'plovdiv1', location: { lat: 42.136097, lng: 24.742168 }, title: 'Пловдив 1',
      adress: 'Южна промишлена зона, Южен, ул. „Македония“ 2, 4013 Пловдив, България', 
      content: 'В магазин "Фантастико", отляво на входа', 
      image: sample1
    },
    { key: 'plovdiv2', location: { lat: 42.14, lng: 24.75 }, title: 'Пловдив 2',
      adress: 'Южна промишлена зона, Южен, ул. „Македония“ 2, 4013 Пловдив, България', 
      content: 'В магазин "Фантастико", отляво на входа', 
      image: sample2 
    },
    { key: 'varna1', location: { lat: 43.204666, lng: 27.910543 }, title: 'Варна 1',
      adress: 'ГРАНД МОЛ, Младост, ул. „Академик Андрей Сахаров“ 2, ниво -2, 9000 Варна', 
      content: 'На паркинг ниво -2', 
      image: sample3
    },
    { key: 'varna2', location: { lat: 43.21, lng: 27.92 }, title: 'Варна 2',
      adress: 'ГРАНД МОЛ, Младост, ул. „Академик Андрей Сахаров“ 2, ниво -2, 9000 Варна', 
      content: 'На паркинг ниво -2', 
      image: sample4
    },
    { key: 'sofia1', location: { lat: 42.68, lng: 23.319941 }, title: 'София 1',
      adress: 'Белите Брези, бул. България, 1680 София, България', 
      content: 'Бензиностанция "Shell", след касите', 
      image: sample1
    },
    { key: 'sofia2', location: { lat: 42.698334, lng: 23.31 }, title: 'София 2',
      adress: 'Белите Брези, бул. България, 1680 София, България', 
      content: 'Бензиностанция "Shell", след касите', 
      image: sample2
    },
    { key: 'sofia3', location: { lat: 42.67, lng: 23.32 }, title: 'София 3',
      adress: 'Белите Брези, бул. България, 1680 София, България', 
      content: 'Бензиностанция "Shell", след касите', 
      image: sample3
    },
    { key: 'burgas1', location: { lat: 42.510578, lng: 27.461014 }, title: 'Бургас 1',
      adress: 'Бургас Център, бул. „Сан Стефано“ 129, 8001 Бургас', 
      content: 'Галерия Бургас, ет. 3, пред Zara', 
      image: sample4
    },
    { key: 'burgas2', location: { lat: 42.52, lng: 27.45 }, title: 'Бургас 2',
      adress: 'Бургас Център, бул. „Сан Стефано“ 129, 8001 Бургас', 
      content: 'В магазин "Фантастико", отляво на входа', 
      image: sample2
    },
  ];

  const PoiMarkers = (props) => {
    const map = useMap();
    const [markers, setMarkers] = useState({});
    const clusterer = useRef(null);

    const [selectedMarker, setSelectedMarker] = useState(null);

    // Initialize MarkerClusterer, if the map has changed
    useEffect(() => {
      if (!map) return;
      if (!clusterer.current) {
        clusterer.current = new MarkerClusterer({map});
      }
    }, [map]);

    // Update markers, if the markers array has changed
    useEffect(() => {
      clusterer.current?.clearMarkers();
      clusterer.current?.addMarkers(Object.values(markers));
    }, [markers]);

    const setMarkerRef = (marker, key) => {
      if (marker && markers[key]) return;
      if (!marker && !markers[key]) return;
  
      setMarkers(prev => {
        if (marker) {
          return {...prev, [key]: marker};
        } else {
          const newMarkers = {...prev};
          delete newMarkers[key];
          return newMarkers;
        }
      });
    };

    const handleClick = useCallback((poi) => {
      setSelectedMarker(poi);
      if (map) {
        map.panTo(poi.location); 
      }
    }, [map]);

    const handleInfoWindowClose = useCallback(() => {
      setSelectedMarker(null);
    }, []);  

    return (
      <>
        {props.pois.map((poi) => (
          <AdvancedMarker 
            key={poi.key} 
            position={poi.location} 
            ref={marker => setMarkerRef(marker, poi.key)}
            clickable={true}
            onClick={() => handleClick(poi)}
          >
            <Pin background="#cc2151" glyphColor="#ffffff" borderColor="#ffbfd1" />
          </AdvancedMarker>
        ))}

        {selectedMarker && (
          <InfoWindow
            position={selectedMarker.location}
            onCloseClick={handleInfoWindowClose} // Close the InfoWindow
          >
            <div>
              <img className="max-h-80 size-fit" src={selectedMarker.image}></img>
              <br></br>
              <h1 className="font-bold text-lg">{selectedMarker.title}</h1>
              <br></br>
              <p><b>Адрес: </b>{selectedMarker.adress}</p>
              <p><b>Насоки: </b>{selectedMarker.content}</p>
              <p><b>Координати: </b>: {selectedMarker.location.lat}, {selectedMarker.location.lng}</p>
            </div>
          </InfoWindow>
        )}
      </>
    );
  };
  
  return (
    <div className="Locations">
      <NavbarSimple />
      <APIProvider apiKey={API_KEY} onLoad={() => console.log('Maps API has loaded.')}> 
        <Map
          className="map"
          defaultZoom={window.innerWidth < 1000 ? 6.90 : 8.15}
          defaultCenter={{ lat: 42.75, lng: 25.4 }}
          mapId="da4d1c57e792e5d8"
          onCameraChanged={(event) => 
            console.log('camera changed:', event.detail.center, 'zoom:', event.detail.zoom)
          }
        >
          <PoiMarkers pois={locations} />
        </Map>
      </APIProvider>
    </div>
  );
}

export default Locations;
