import React from 'react';
import {NavbarSimple} from './Navbar.js'; 
import ContactForm from "./ContactForm";
import './Contact.css';

function Contact() {
    return(
        <div>
            <NavbarSimple />
            <div class="flex flex-col items-center justify-center outer-pane pt-16 p-3">
                <div className='w-4/5 text-4xl text-center font-semibold'>
                    Имаш въпрос или искаш да ни дадеш обратна връзка?            
                </div>
                <div className='w-4/5 text-2xl text-center m-3'>
                    Използвай формата за бърз контакт или предоставената по-долу информация и за нас ще е удоволствие да отговорим на запитването в най-кратък срок!            
                </div>
                <div className='w-4/5 m-12 inner-pane pb-5 p-10'>            
                    <ContactForm />
                </div>
                <div className='w-4/5 text-2xl'>
                    Телефон: <b>0891234567</b>
                </div>
                <div className='w-4/5 text-2xl'>
                    Имейл: <b>ivan.ivanov@gmail.com</b>
                </div>
            </div>
        </div>
    );
}

export default Contact;